//IMPORTAR VARIABLES
@use 'variables' as var;

//ESTABLECE LA PAGUINA A UN SOLO FORMATO GLOBAL
html{
    font-size: 62.5%;
    box-sizing: border-box;

    scroll-behavior: smooth;
}

*,
*::before,
*::after{
    box-sizing: inherit;
}

body{
    font-family: var.$fuente_principal;
    font-size: 1.6rem;

    &.fijar-body{
        overflow: hidden; //BLOQUEA EL SCROL DEL BODY
    }

    &.body-scroll{
        padding-top: 80px;
    }
}

p{
    font-size: 2rem;
    color: var.$negro;
    line-height: 1.5;
}

.contenedor{
    width: 95%;
    max-width: 150rem;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

h1, h2, h3{
    margin: 0 0 5rem 0;
}

h1{
    font-size: 4rem;
}

h2{
    font-size: 3.6rem;
}

h3{
    font-size: 3rem;
}

img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}

